<template>
  <div class="head">
    <van-nav-bar :title="title" left-text="" :class="[{'active': headerActive},{'back-hader':type === 'back'}]" left-arrow>
      <template #left>
        <template v-if="type === 'back'">
          <img class="back" src="@/assets/images/back@2x.png" @click="$goBack" alt="">
        </template>
        <template v-else>
          <span class="header-title">
            <slot name="leftTitle"></slot>
          </span>
        </template>
      </template>
      <template #right v-if="type !== 'back'">
        <van-popover v-model:show="showPopover" placement="bottom-end" :actions="actions" @select="changeLanuage">
          <template #reference>
            <div class="flex-center">
              <!-- {{localFilter(local)}}
              <img class="arrDown" :class="{'arrUp': showPopover}" src="@/assets/images/arr-b.png"> -->
              <img class="lang" src="@/assets/images/lang@2x.png" alt="">
            </div>
          </template>
        </van-popover>
        <img class="header-icon" src="@/assets/images/more.png" @click="onMenu" alt="" />
      </template>

    </van-nav-bar>
    <div class="menu-Block" v-show="open" @click.self="closeMenu()">
      <div class="menu-leftBlock">
        <div class="menu-item flex-middle" @click="$router.push({name:'Index'})">
          <div class="left flex-item flex-middle">
            <img class="menu-img" src="@/assets/images/nav/1.png" />
            <div class="menu-tit flex-item flex-between">
              {{ $t("index.首頁") }}
            </div>
          </div>
          <img class="arr" src="@/assets/images/arr-r.png" alt="">
        </div>
        <div class="menu-item flex-middle" @click="$router.push({name:'Ido'})">
          <div class="left flex-item flex-middle">
            <img class="menu-img" src="@/assets/images/nav/2.png" />
            <div class="menu-tit flex-item flex-between">
              {{ $t("index.IDO") }}
            </div>
          </div>
          <img class="arr" src="@/assets/images/arr-r.png" alt="">
        </div>
        <div class="menu-item flex-middle" @click="$router.push({name:'Share'})">
          <div class="left flex-item flex-middle">
            <img class="menu-img" src="@/assets/images/nav/3.png" />
            <div class="menu-tit flex-item flex-between">
              {{ $t("index.分享邀請") }}
            </div>
          </div>
          <img class="arr" src="@/assets/images/arr-r.png" alt="">
        </div>
        <!-- @click="$router.push({name:'Top'})" -->
        <!-- <div class="menu-item flex-middle" @click="$isDevelopment()">
          <div class="left flex-item flex-middle">
            <img class="menu-img" src="@/assets/images/nav/4.png" />
            <div class="menu-tit flex-item flex-between">
              {{ $t("index.TOP數據儀錶盤") }}
            </div>
          </div>
          <img class="arr" src="@/assets/images/arr-r.png" alt="">
        </div> -->
        <!-- @click="$router.push({name:'Account'})" -->
        <!-- <div class="menu-item flex-middle" @click="$isDevelopment()">
          <div class="left flex-item flex-middle">
            <img class="menu-img" src="@/assets/images/nav/5.png" />
            <div class="menu-tit flex-item flex-between">
              {{ $t("index.我的賬戶") }}
            </div>
          </div>
          <img class="arr" src="@/assets/images/arr-r.png" alt="">
        </div> -->
        <!-- <div class="menu-item flex-middle" @click="$isDevelopment()">
          <div class="left flex-item flex-middle">
            <img class="menu-img" src="@/assets/images/nav/6.png" />
            <div class="menu-tit flex-item flex-between">
              {{ $t("index.計算器") }}
            </div>
          </div>
          <img class="arr" src="@/assets/images/arr-r.png" alt="">
        </div> -->
        <div class="menu-item flex-middle" @click="$router.push({name:'Team'})">
          <div class="left flex-item flex-middle">
            <img class="menu-img" src="@/assets/images/nav/7.png" />
            <div class="menu-tit flex-item flex-between">
              {{ $t("index.我的團隊") }}
            </div>
          </div>
          <img class="arr" src="@/assets/images/arr-r.png" alt="">
        </div>
        <div class="menu-item flex-middle" @click="$router.push({name:'Nft'})">
          <div class="left flex-item flex-middle">
            <img class="menu-img" src="@/assets/images/nav/8.png" />
            <div class="menu-tit flex-item flex-between">
              {{ $t("index.星球NFT") }}
            </div>
          </div>
          <img class="arr" src="@/assets/images/arr-r.png" alt="">
        </div>
        <div class="menu-item flex-middle" @click="$isDevelopment()">
          <div class="left flex-item flex-middle">
            <img class="menu-img" src="@/assets/images/nav/9.png" />
            <div class="menu-tit flex-item flex-between">
              {{ $t("index.鏈遊") }}
            </div>
          </div>
          <img class="arr" src="@/assets/images/arr-r.png" alt="">
        </div>
        <div class="menu-item flex-middle" >
          <a class="left flex-item flex-middle" href="/happyplanet.pdf" target="_blank">
            <img class="menu-img" src="@/assets/images/nav/10.png" />
            <div class="menu-tit flex-item flex-between">
              {{ $t("index.白皮書") }}
            </div>
          </a>
          <img class="arr" src="@/assets/images/arr-r.png" alt="">
        </div>
      </div>
    </div>
    <MyDialog v-model:isShow="isDialogPrompt" :title="$t('index.註冊綁定')" :isShade="isShade">
      <template #content>
        <div class="address" style="padding-top:12px;">{{intro}}</div>
      </template>
      <template #footer>
        <div class="btn-wrap dialog-btn" style="padding-top:0;">
          <van-button type="primary" :loading="bindBtn.loading" :disabled="bindBtn.loading || bindBtn.disabled" @click="bindIntroHandle" :loading-text="$t('index.確定')+'...'">{{ $t("index.確定") }}</van-button>
        </div>
      </template>
    </MyDialog>
  </div>
</template>

<script>
import { reactive, toRefs, computed, watch, getCurrentInstance } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import { getLang, setLang } from "@/utils/i18n";
import { intro } from "@/storage"
import MyDialog from "@/components/MyDialog";
import { config } from "@/utils/config";
import web3Data from "@/utils/initWeb3";
import introAbi from "@/utils/introAbi.json";

import {
  Toast
} from "vant";
export default {
  components: {
    MyDialog
  },
  props: {
    type: {
      type: String,
      default: ""
    },
    title: {
      type: String,
      default: ""
    }
  },
  setup (props) {
    const { proxy } = getCurrentInstance();
    const store = useStore();
    const { t } = useI18n();
    const data = reactive({
      t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      type: props.type,
      title: props.title,
      showPopover: false,
      headerActive: false,
      local: getLang() || "zh-TW",
      intro: intro.get() || '',
      actions: [
        {
          text: 'English',
          value: 'en'
        },
        {
          text: '中文繁體',
          value: 'zh-TW'
        }
        // {
        //   text: '日本語',
        //   value: 'ja'
        // },
        // {
        //   text: '한국어',
        //   value: 'ko'
        // }
      ],
      open: false,
      isDialogPrompt: false,
      isShade: false,
      introContract: null,
      bindBtn: {
        loading: false,
        disabled: false
      },
      address0: '0x0000000000000000000000000000000000000000',
      initContract: () => {
        data.introContract = new data.web3.eth.Contract(
          introAbi,
          config.INTRO
        );
        data.initData();
      },
      initData: () => {
        data.getIntroHandle()
      },
      onMenu: () => {
        data.open = !data.open;
      },
      closeMenu: () => {
        data.open = false;
      },
      changeLanuage: (val) => {
        setLang(val.value)
        data.local = val.value
        location.reload()
      },
      localFilter (value) {
        let list = data.actions
        let res = ''
        for (let k = 0; k < list.length; k++) {
          if (list[k].value === value) {
            res = list[k].text
            break
          }
        }
        return res
      },
      getIntroHandle: async () => {
        const Toast0 = Toast.loading({
          duration: 0,
        });
        try {
          let res = await data.introContract.methods.getIntro(data.address).call({
            from: data.address
          });
          console.log(res, 'getIntro')
          if (res === data.address0) {
            // 沒有推薦人
            data.bindIntroPre()
          }
          Toast0.clear()
        } catch (error) {
          Toast0.clear()
          proxy.$errorformat(error)
        }
      },
      bindIntroPre: async () => {
        if (data.address !== config.vertex) {
          data.isDialogPrompt = true
          if (!data.intro) {
            data.intro = t("index.推薦地址无效2")
            data.bindBtn.disabled = true
            return false
          }
          if (!web3Data.web3.utils.isAddress(data.intro)) {
            Toast(t("index.推薦地址无效1"));
            data.bindBtn.disabled = true
            return false
          }
          let res = await data.introContract.methods.getIntro(data.intro).call({
            from: data.address
          });
          console.log(res, 'getIntro.推薦人')
          if (res === data.address0 && data.intro !== config.vertex) {
            // 推薦人無上級
            Toast(t("index.推薦地址无效3"));
            data.bindBtn.disabled = true
            return false
          }
          return true
        } else {
          console.log('不需綁定推薦人，此為頂點')
          data.isDialogPrompt = false
        }
      },
      bindIntroHandle: async () => {
        let flag = data.bindIntroPre()
        if (flag) {
          data.bindBtn.loading = true
          try {
            let res = await data.introContract.methods.bindIntro(data.intro, data.address).send({
              from: data.address
            });
            console.log(res, 'bindIntro')
            Toast(t("index.Success"));
            data.bindBtn.loading = false
            data.isShade = true
            data.isDialogPrompt = false
          } catch (error) {
            data.bindBtn.loading = false
            proxy.$errorformat(error)
          }
        } else {
          data.isDialogPrompt = false
        }
      }
    })
    const debounce = (fn, interval) => {
      var timer;
      var gapTime = interval || 150;
      return function () {
        clearTimeout(timer);
        var context = this;
        var args = arguments;
        timer = setTimeout(function () {
          fn.call(context, args);
        }, gapTime);
      };
    }
    document.addEventListener('scroll', debounce(() => {
      let scrollTop = document.documentElement.scrollTop || document.body.scrollTop
      data.headerActive = scrollTop > 30
    }))
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
};
</script>
<style lang="less" scoped>
.lang {
  width: 21px;
  height: 21px;
  display: block;
  margin-left: 10px;
}
.head {
  color: #b1beea;
  font-size: 14px;
  font-family: PingFang SC;
  font-weight: 500;
  line-height: 35px;
}
.back {
  width: 10px !important;
  height: 18px;
}
.arrDown {
  width: 11px !important;
  height: 11px;
  margin-left: 8px;
}
.arrUp {
  transform: rotateX(180deg);
}
.header-title {
  font-size: 22px;
  font-weight: 600;
  color: #ffffff;
}
.header-icon {
  width: 18px;
  height: 16px;
  display: block;
  margin-left: 23px;
}
.menu-Block {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 499;
  .menu-leftBlock {
    position: absolute;
    right: 0;
    top: 46px;
    bottom: 0;
    background: #2a1857;
    left: 31%;
    .menu-item {
      padding: 12.5px 10px 12.5px 21px;
      line-height: 1.3;
      .menu-img {
        width: 18px;
        height: 18px;
        display: block;
      }
      .menu-tit {
        font-size: 14px;
        color: #ffffff;
        margin-left: 11px;
        span {
          font-size: 16px;
          color: #ff9600;
        }
      }
      .arr {
        width: 7px;
        height: 12px;
        display: block;
      }
    }
  }
}
</style>
<style lang="less">
#app {
  .van-nav-bar {
    &.active {
      transition: all 0.3s;
      background: #35305a;
    }
    &.back-hader {
      transition: all 0.3s;
      background: #040306;
      box-shadow: initial;
    }
  }
}
</style>
